<template>
    <div>
       <a-form :form="productWarpForm2">
            <a-layout style="background:none;">
            <a-layout-header> <span></span></a-layout-header>
            <a-layout>
                <a-layout-content style="flex: 0 0 900px;min-width:900px;max-width: 900px;width: 900px;">
                    <a-form-item v-if="productId !==-1" label="" class="required"  :label-col="{ span: 5}" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productWarpForm2.productCode"  :disabled="productId !==-1"/>
                    </a-form-item>
                    <a-form-item label="" class="required"  :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productWarpForm2.productName" />
                    </a-form-item>
                    <a-form-item label=""  class="required" :label-col="{ span: 5 }"  :wrapper-col="{ span: 19 }" >
                    <a-tree-select 
                            style="width: 280px"
                            :dropdownStyle="{ maxHeight: '400px', overflow: 'auto' }"
                            :treeData="treeData1"
                            @select="changeclaId"
                            placeholder=''
                            treeDefaultExpandAll
                            v-model="treeselectvalue"
                            >
                        </a-tree-select>
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productWarpForm2.productDescribe" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productWarpForm2.productSpecs" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productWarpForm2.productChart" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productWarpForm2.productModel" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productWarpForm2.productMaterialQuality" />
                    </a-form-item>
                </a-layout-content>
                <a-layout-sider  style="background:none;height:180px;text-align:center ">
                <a-form-item style="width:100%">
                    <a-upload 
                        name="file"
                        class="avatar-uploader picupload"
                        :action="fileUploadUrl" 
                        :multiple="true" 
                        listType="picture-card"
                        :fileList="productPictureList" 
                        @change="handleChange3"
                        :beforeUpload="beforeUpload"
                        :showUploadList="false">
                        <img v-if="imageUrl3" :src="imageUrl3" alt="avatar"  style="width:180px;height:180px"/>
                        <div v-else>
                            <a-icon :type="loading ? 'loading' : 'plus'" />
                            <div class="ant-upload-text"></div>
                        </div>
                    </a-upload>
                </a-form-item>
                </a-layout-sider>
            </a-layout>
            </a-layout>
        </a-form>
    </div>
</template>
<script>
    import {getProductNoTreeList,getProductInfo,getAttributesList} from '@/api/ProductApi';
    function getBase64 (img, callback) {
        const reader = new FileReader()
        reader.addEventListener('load', () => callback(reader.result))
        reader.readAsDataURL(img)
    }
    export default {
         props: ['productId','ProductForm'],
        data() {
            return {
                showPagination: false,
                confirmLoading: false,
                curproductCode:null,
                curclaCode:null,
                loading: false,
                productWarpForm2:{
                    productChart: "",        //
                    productCode: "",        //
                    productDescribe: "",        //
                    productMaterialQuality: "",        //
                    productModel: "",        //
                    productName: "",        //
                    productSpecs: "",        //
                    productPicture:null,       //
                    claCode:null,       //
                    productPackingList:[]
                },
                treeData1:[],
                treeselectvalue:"",
                productPictureList: [],
                imageUrl3: '',
                
            }
        },
        watch: {}, 
        computed: {
            fileUploadUrl() {
                return this.$global.baseUrl + '/file/upload' + '?access_token=' + this.$global.access_token;
            },
        },
        created(){
        this.getProductNoTreeLists();
        this.getProductInfos(this.productId)
        },
        methods: {
            //productId
            getProductInfos(corId){
                // console.log("this.ProductForm")
                // console.log(this.ProductForm)
                //this.attributesdata = JSON.parse(JSON.stringify(this.attributesdata));
                if(corId !== -1){
                    getProductInfo(corId).then((res) => {
                        if (res.data.code === this.$global.successCode) {
                        //console.log("",res);
                        setTimeout(() => {//warpForm
                          //
                          this.productWarpForm2.productCode = res.data.data.productCode;
                          this.productWarpForm2.productDescribe = res.data.data.productDescribe;
                          this.productWarpForm2.productMaterialQuality = res.data.data.productMaterialQuality;
                          this.productWarpForm2.productModel = res.data.data.productModel;
                          this.productWarpForm2.productName = res.data.data.productName;
                          this.productWarpForm2.productChart =res.data.data.productChart;
                          this.productWarpForm2.productSpecs =res.data.data.productSpecs;
                          this.productWarpForm2.productPicture =Number(res.data.data.productPicture);
                          this.productWarpForm2.claCode =res.data.data.claCode;

                          console.log("this.productWarpForm2.claCode")
                          console.log(this.productWarpForm2.claCode)
                        }, 0);

                        //
                        this.ProductForm.productAttributesList =[]
                        if(res.data.data.productAttributesList.length){
                            this.ProductForm.productAttributesList = res.data.data.productAttributesList
                        }else{
                            getAttributesList({claCode:res.data.data.claCode}).then((res) => {
                            if (res.data.code === this.$global.successCode) {
                            console.log("",res);
                                res.data.data.forEach(item =>{
                                    item.attId = null;
                                    item.orderid = null;
                                    item.productCode = this.productWarpForm2.productCode
                                    this.ProductForm.productAttributesList.push(item)
                                });

                                }else{
                                    this.$message.error(res.data.message);
                                }
                                }, function (error) {
                                    console.log("", error);
                            });

                        }

                        this.$emit("changeComponent1Data", this.ProductForm)

                        //claCode
                        this.treeselectvalue = res.data.data.claCode
                        

                        //productPicture
                        this.imageUrl3=res.data.data.productPictureUrl

                        }else{
                            this.$message.error(res.data.message);
                        }
                        }, function (error) {
                            console.log("", error);
                    });
                }   
            },
            //
            getProductNoTreeLists() {
                getProductNoTreeList().then(res => {
                    let arr=[]
                    if (res.data.code === this.$global.successCode) {
                        //console.log("", res);
                        res.data.data.forEach(element => {
                        arr.push(element);
                        });
                        this.treeData1=arr
                    }else{
                            this.$message.error(res.data.message);
                        }
                },
                function(error) {
                    console.log("", error);
                }
                );
            },
            changeclaId(){
                console.log(arguments[0])
                this.curclaCode = arguments[0]
                this.productWarpForm2.claCode = arguments[0]

                // console.log("===========claCode:");
                // console.log(this.ProductForm);

                this.ProductForm.productAttributesList =[]
                getAttributesList({claCode:this.curclaCode}).then((res) => {
                if (res.data.code === this.$global.successCode) {
                console.log("",res);
                    res.data.data.forEach(item =>{
                        item.attId = null;
                        item.orderid = null;
                        item.productCode = this.productWarpForm2.productCode
                        this.ProductForm.productAttributesList.push(item)
                     });

                    this.$emit("changeComponent1Data", this.ProductForm)

                    }else{
                        this.$message.error(res.data.message);
                    }
                    }, function (error) {
                        console.log("", error);
                });

            },
            //
            handleChange3(info) {
                
                let fileList = [...info.fileList];
                fileList = fileList.slice(-2);
                fileList = fileList.map((file) => {
                    //console.log(file);
                    if (file.response) {
                
                    getBase64(info.file.originFileObj, (imageUrl) => {
                        this.imageUrl3 = imageUrl
                        })

                        this.productWarpForm2.productPicture = file.response.data.id
                        
                    }
                    return file;
                });
                this.productPictureList = fileList
            },
            beforeUpload (file) {
                    const isJPG = file.type === 'image/jpeg'
                    const isPNG = file.type === 'image/png'
                    if (!isJPG && !isPNG) {
                        this.$message.error('jpgpng!')
                    }
                    const isLt2M = file.size / 1024 / 1024 < 2
                    if (!isLt2M) {
                        this.$message.error('2MB!')
                    }
                    return isJPG || isPNG && isLt2M
                },
        },
    }
</script>

<style scoped>
.required >>> .ant-form-item-label::before{ content:'* ';font-weight:bold;color:#ff0036;}
.ant-form-item-label {text-align: right; vertical-align: middle;line-height: 39.9999px;display: inline-block;
overflow: hidden;overflow-x: hidden; overflow-y: hidden; white-space: nowrap;}
.ant-layout-header {background: none;padding:0px 5px;height: 44px;line-height: 34px;font-size: 16px;margin-top: -10px;}
.title {font-size: 18px;padding: 0px 20px 15px;}
</style>
      
      

