<template>
    <div>
        <a-button type="primary"   @click="handleAddTuopan" style="margin-bottom:15px"></a-button>
            <a-table :columns="tuopancolumns" :dataSource="curPackingForms.productPalletList"   :pagination="showPagination"  :rowKey="(v,i)=>{return i}">  
            <template slot="type" slot-scope="text, record">
                <span class="lineEllipsis2"><editable-cell :text="text" @change="onCellChange(record, 'type', $event)"/></span>
            </template>
            <template slot="length" slot-scope="text, record">
                <span class="lineEllipsis2"><editable-cell :text="text" @change="onCellChange(record, 'length', $event)"/></span>
            </template>
            <template slot="width" slot-scope="text, record">
                <span class="lineEllipsis2"><editable-cell :text="text" @change="onCellChange(record, 'width', $event)"/></span>
            </template>
            <template slot="height" slot-scope="text, record">
                <span class="lineEllipsis2"><editable-cell :text="text" @change="onCellChange(record, 'height', $event)"/></span>
            </template>
            <template slot="load" slot-scope="text, record">
                <span class="lineEllipsis2"><editable-cell :text="text" @change="onCellChange(record, 'load', $event)"/></span>
            </template>
            <template slot="remark" slot-scope="text, record">
                <span class="lineEllipsis2"><editable-cell :text="text" @change="onCellChange(record, 'remark', $event)"/></span>
            </template>
            <template slot="action" slot-scope="text,record,index">
                <a-popconfirm  v-if="curPackingForms.productPalletList.length"  title="?"  @confirm="() => onDelete2(index,record)"  cancelText=""  okText="">
                    <a href="javascript:;" style="color:#00aaee"> <a-icon type="delete" /></a>
                </a-popconfirm>
            </template>
        </a-table>
    </div>
</template>
<script>
    import EditableCell from './EditableCell';
    const tuopancolumns = [
        {
        title: '',
        dataIndex: 'type',
        key: 'type',
        scopedSlots: { customRender: 'type' },
        width:200,
        align:'center'
        }, {
        title: '(cm)',
        dataIndex: 'length',
        key: 'length',
        scopedSlots: { customRender: 'length' },
        width:200,
        align:'center'
        }, {
        title: '(cm)',
        dataIndex: 'width',
        key: 'width',
        scopedSlots: { customRender: 'width' },
        width:200,
        align:'center'
        }, {
        title: '(cm)',
        dataIndex: 'height',
        key: 'height',
        scopedSlots: { customRender: 'height' },
        width:200,
        align:'center'
        }, {
        title: '(kg)',
        dataIndex: 'load',
        key: 'load',
        scopedSlots: { customRender: 'load' },
        width:200,
        align:'center'
        }, {
        title: '',
        dataIndex: 'remark',
        key: 'remark',
        scopedSlots: { customRender: 'remark' },
        width:200,
        align:'center'
        }, { 
        title: '',
        width:200,
        align:'center',
        scopedSlots: { customRender: 'action' },
        },
    ];

    export default {
         props: ['curPackingForms'],
    components: {
        EditableCell,
        },
        data() {
            return {
                showPagination: false,
                confirmLoading: false,
                tuopandata:[],
                tuopancolumns,
                tuopancount:0,
                loading: false,
            }
        },
        watch: {}, 
        computed: {
     
        },
        created(){
            console.log( this.curPackingForms)
        },
        methods: {

             //table
            onDelete2 (key,corRow) {
                this.curPackingForms.productPalletList.splice(key,1);
                console.log("this.curPackingForms.delProductPalletList")
                console.log(this.curPackingForms.delProductPalletList)
                this.curPackingForms.delProductPalletList.push(corRow);
                this.$emit("changeCallBack", this.curPackingForms);
            },
            //
            onCellChange (corRow, dataIndex, value) {
                const data1 = [...this.curPackingForms.productPalletList]
                const target = data1.find(item => item.key === corRow.key)

                if (target) {

                    target[dataIndex] = value
                    this.curPackingForms.productPalletList = data1

                    console.log("this.curPackingForms.productPalletList")
                    console.log(this.curPackingForms.productPalletList)

                    this.$emit("changeCallBack", this.curPackingForms);
                }
            },
            //table
            handleAddTuopan () {
               // const { tuopancount, tuopandata } = this
                const newData = {
                    key: this.tuopancount,
                    height: `0`,
                    length: `0`,
                    load: `0`,
                    type: `0`,
                    width: `0`,
                    remark: `0`,
                }
                this.curPackingForms.productPalletList = [...this.curPackingForms.productPalletList, newData]
                this.tuopancount = this.tuopancount + 1
            },  
        },
    }
</script>

<style scoped>
