.editable-cell {position: relative;}
.ant-breadcrumb { padding: 0;position: absolute;top: 10px;right: 20px;}
.dialog_content { padding: 0;}
.editable-cell {position: relative;}
.editable-cell-input-wrapper,
.editable-cell-text-wrapper { padding-right: 24px;}
.editable-cell-text-wrapper { padding: 5px 24px 5px 5px;}
.editable-cell-icon,
.editable-cell-icon-check {position: absolute;right: 0;width: 20px;cursor: pointer;}
.editable-cell-icon {line-height: 18px;display: none;}
.editable-cell-icon-check {line-height: 28px;}
.editable-cell:hover .editable-cell-icon { display: inline-block;}
.editable-cell-icon:hover,
.editable-cell-icon-check:hover { color: #108ee9;}
.editable-add-btn {margin-bottom: 8px;}
.ant-form-item-label {
  text-align: right;
  vertical-align: middle;
  line-height: 39.9999px;
  display: inline-block;
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
}
