<template>
    <div>
        <a-button type="primary"   @click="showModal4" style="margin-bottom:15px"></a-button>
        <a-table :columns="documentationcolumns" :dataSource="documentationdata"  :pagination="showPagination" :rowKey="(v,i)=>{return i}">
            <template slot="docName" slot-scope="text, record">
                <a :href="downloadDocment(record)"  style="cursor: pointer"><span class="lineEllipsis2">{{text}}</span></a>
            </template>
            <template slot="docCode" slot-scope="text, record">
            <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="docEdition" slot-scope="text, record">
            <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="docLegend" slot-scope="text, record">
            <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="docState" slot-scope="text, record">
            <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="fileId" slot-scope="text, record">
            <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="action" slot-scope="text,record,index">
                <a href="javascript:;"  @click="editDoc(index,record)"> <a-icon type="edit" /></a>
                <a-popconfirm  v-if="documentationdata.length"  title="?"  @confirm="() => onDeletedoc(index,record)"  cancelText=""  okText="">
                    <a href="javascript:;"  > <a-icon type="delete" /></a>
                </a-popconfirm>
            </template>
        </a-table>

        <!--  -->
        <a-modal title="" :visible="visible4"  class="dialog_content" width="600px" :confirmLoading="confirmLoading" @cancel="visible4=false"  @ok="addProductDocumentation"  cancelText=""  okText="">
            <div class="dialog_form dialog_form3">
                <a-form :form="addProductDocumentationForms2">
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-input  placeholder="" v-decorator="[ 'docCode']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4}" :wrapper-col="{ span: 20 }" >
                        <a-input   placeholder="" v-decorator="[ 'docState']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-input  placeholder="" v-decorator="[ 'docLegend']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-input  placeholder="" v-decorator="[ 'docEdition']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-upload :action="fileUploadUrl" :multiple="true" :fileList="documentationFileList" @change="handleChange2">
                            <a-button><a-icon type="upload" /> </a-button>
                        </a-upload>
                    </a-form-item>
                </a-form>
            </div>
        </a-modal>

        <!--  -->
        <a-modal title="" :visible="visible5"  class="dialog_content" width="600px" :confirmLoading="confirmLoading"  @cancel="visible5=false"  @ok="editProductDocumentation"  cancelText=""  okText="">
            <div class="dialog_form dialog_form3">
                <a-form :form="addProductDocumentationForms3">
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-input  placeholder="" v-decorator="[ 'docCode']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4}" :wrapper-col="{ span: 20 }" >
                        <a-input   placeholder="" v-decorator="[ 'docState']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-input  placeholder="" v-decorator="[ 'docLegend']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-input  placeholder="" v-decorator="[ 'docEdition']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }" >
                        <a-upload :action="fileUploadUrl" :multiple="true" :fileList="documentationFileList5" @change="handleChange5">
                            <a-button><a-icon type="upload" /> </a-button>
                        </a-upload>
                    </a-form-item>
                </a-form>
            </div>
        </a-modal>
    </div>
</template>
<script>
   import {getProductInfo} from '@/api/ProductApi';

    const documentationcolumns = [
        {
        title: '',
        dataIndex: 'docName',
        key: 'docName',
        width:200,
        scopedSlots: { customRender: 'docName' },
        align:'center'
        }, {
        title: '',
        dataIndex: 'docCode',
        key: 'docCode',
        width:200,
        scopedSlots: { customRender: 'docCode' },
        align:'center'
        }, {
        title: '',
        dataIndex: 'docEdition',
        key: 'docEdition',
        width:200,
        scopedSlots: { customRender: 'docEdition' },
        align:'center'
        }, {
        title: '',
        dataIndex: 'docLegend',
        key: 'docLegend',
        width:200,
        scopedSlots: { customRender: 'docLegend' },
        align:'center'
        }, {
        title: '',
        dataIndex: 'docState',
        key: 'docState',
        width:200,
        scopedSlots: { customRender: 'docState' },
        align:'center'
        }, { 
        title: '',
        width:200,
        align:'center',
        scopedSlots: { customRender: 'action' },
        },
    ];
  
    export default {
        props: ['productId'],
    components: {},
        data() {
            return {
                currentDocUrl:"",
                showPagination:false,
                documentationFileList: [],
                documentationFileList5: [],
                confirmLoading: false,
                addProductDocumentationForms2: this.$form.createForm(this),
                addProductDocumentationForms3: this.$form.createForm(this),

                ProductForm2:
                {
                    claCode:"",
                    delProductAttributesList: [
                        // {
                        // "attId:null,
                        // "attIsempty:null,
                        // "attName:"",
                        // "attValue:"",
                        // "orderid:null,
                        // "productCode:""
                        // }
                    ],
                    delProductDocumentationList: [
                        // {
                        // "docCode:"",
                        // "docEdition:"",
                        // "docId:null,
                        // "docLegend:"",
                        // "docName:"",
                        // "docState:null,
                        // "fileId:null,
                        // "productCode:""
                        // }
                    ],
                    delProductDrawingList: [
                        // {
                        // "drawCode:"",
                        // "drawId:null,
                        // "drawName:"",
                        // "fileId:null,
                        // "itemCode:"",
                        // "productCode:""
                        // }
                    ],
                    delProductPackingList: [
                        // {
                        // "addPalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "deletePalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "packHeight:null,
                        // "packId:null,
                        // "packLength:null,
                        // "packStandards:"",
                        // "packVolume:null,
                        // "packWeight:null,
                        // "packWidth:null,
                        // "productCode:"",
                        // "remark:""
                        // }
                    ],
                    productAttributesList: [
                        // {
                        // "attId:null,
                        // "attIsempty:null,
                        // "attName:"",
                        // "attValue:"",
                        // "orderid:null,
                        // "productCode:""
                        // }
                    ],
                    productChart:"",
                    productCode:"",
                    productDescribe:"",
                    productDocumentationList: [
                        // {
                        // "docCode:"",
                        // "docEdition:"",
                        // "docId:null,
                        // "docLegend:"",
                        // "docName:"",
                        // "docState:null,
                        // "fileId:null,
                        // "productCode:""
                        // }
                    ],
                    productDrawingList: [
                        // {
                        // "drawCode:"",
                        // "drawId:null,
                        // "drawName:"",
                        // "fileId:null,
                        // "itemCode:"",
                        // "productCode:""
                        // }
                    ],
                    productId:null,
                    productMaterialQuality:"",
                    productModel:"",
                    productName:"",
                    productPackingList: [
                        // {
                        // "addPalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "deletePalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "packHeight:null,
                        // "packId:null,
                        // "packLength:null,
                        // "packStandards:"",
                        // "packVolume:null,
                        // "packWeight:null,
                        // "packWidth:null,
                        // "productCode:"",
                        // "remark:""
                        // }
                    ],
                    productPicture:null,
                    productRoutine: {
                        productBrand:"",
                        productCode:"",
                        productDiameter:"",
                        productImprint:"",
                        productLength:"",
                        productMaterial:"",
                        productMaterialRemark:"",
                        productStandard:"",
                        productSurface:"",
                        productSurfaceRemark:"",
                        productToothPitch:"",
                        productToothType:"",
                        productUnit:"",
                        productWeight:"",
                        remark:"",
                        routId:null
                    },
                    productSpecs:""
                },
                documentationdata:[],
                documentationdata2:[],
                documentationcolumns,
                visible5: false,
                visible4: false,
                documentationfileId:null,
                documentationName: '',
                documentationUrl: '',
                documentationfileId5:null,
                documentationName5: '',
                documentationUrl5: '',
                loading: false,
                curproductCode:null
                
            }
        },
        watch: {}, 
        computed: {
            fileUploadUrl() {
                return this.$global.baseUrl + '/file/upload' + '?access_token=' + this.$global.access_token;
            },
        },
        created(){
           this.getProductInfos(this.productId);
           
        },
        methods: {
            //
            editDoc(i,v){
                this.visible5=true;
                this.editDocTemp=i
                this.documentationfileId = v.fileId
                this.documentationName =v.docName
                this.documentationUrl = v.fileUrl
                let {
                        docCode,
                        docState,
                        docLegend,
                        docEdition,
                    }=v
                    setTimeout(() => {
                    this.addProductDocumentationForms3.setFieldsValue({
                        docCode,
                        docState,
                        docLegend,
                        docEdition,
                        })
                }, 0);
            },
            editProductDocumentation(){
                this.addProductDocumentationForms3.validateFields((err, values) => {
                if (!err) {
                    console.log("values")
                    console.log(values)
                    const form = values;
                    if(this.documentationfileId5){
                        form.fileId = this.documentationfileId5;
                        form.docName = this.documentationName5;
                        form.fileUrl = this.documentationUrl5;
                    }else{
                        form.fileId = this.documentationfileId;
                        form.docName = this.documentationName;
                        form.fileUrl = this.documentationUrl;
                    }
                    
                    form.docState = values.docState
                    if(this.productId !== -1){
                        form.docId=this.documentationdata[this.editDocTemp].docId
                        
                       }
                    form.productCode= this.curproductCode
                   // console.log("this.editProductDocumentation productCode")
                    //console.log(form.productCode)
                    this.documentationdata[this.editDocTemp]=values;
                    this.visible5 = false
                     if(values.docState){
                       form.docState = Number(values.docState)
                   }
                    this.ProductForm2.productDocumentationList[this.editDocTemp]=form;
                    this.$emit("changeCallBack", this.ProductForm2);
                    this.documentationfileId=null
                    this.documentationName=null
                    this.documentationfileId5=null
                    this.documentationName5=null
                    this.documentationUrl=null
                    this.documentationUrl5=null
                   // console.log("===========productDocumentationList:");
                    //console.log(this.ProductForm2);
                }
                });
            },
          
            //productId
            getProductInfos(corId){
                if(corId !== -1){
                    getProductInfo(corId).then((res) => {
                        if (res.data.code === this.$global.successCode) {
                        //console.log("",res);

                        //
                        this.documentationdata = res.data.data.productDocumentationList
                        this.curproductCode = res.data.data.productCode

                        }else{
                            this.$message.error(res.data.message);
                        }
                        }, function (error) {
                            console.log("", error);
                    });
                }
            },
            
            showModal4() {
                this.documentationFileList =[]
                this.addProductDocumentationForms2.resetFields();
                this.visible4 = true
            },

            //
            addProductDocumentation(e){
               
                e.preventDefault();
                this.addProductDocumentationForms2.validateFields((err, values) => {
                if (!err) {
                    console.log('Received values of form: ', values);
                    if((values.docCode == undefined))
                    {  
                        values.docCode = null
                    }
                    if((values.docEdition == undefined ))
                    {  
                        values.docEdition = null
                    }
                    if((values.docLegend == undefined))
                    {  
                        values.docLegend = null
                    }
                    if((values.docState == undefined))
                    {  
                        values.docState = null
                    }
                    
                    const form = values;
                    if(this.documentationfileId){
                        form.fileId = this.documentationfileId;
                        form.docName = this.documentationName;
                        form.fileUrl = this.documentationUrl
                    }else{
                        form.fileId =  null
                        form.docName =  null
                    }
                    form.docState = values.docState
                    form.productCode= this.curproductCode

                    if(values.docState){
                     form.docState = Number(values.docState)
                   }
                   
                    //console.log("this.productDocumentationList productCode")
                    //console.log(form.productCode)
                    this.documentationdata.push(form);
                    this.visible4 = false

                   
                     
                    this.ProductForm2.productDocumentationList.push(form);
                    this.$emit("changeCallBack", this.ProductForm2);
                    this.documentationfileId=null
                    this.documentationName=null
                    this.documentationUrl=null
                   // console.log("===========productDocumentationList:");
                    //console.log(this.ProductForm2);
                }
                });
            },

            //table
            onDeletedoc(index,corRow){
              //console.log(corRow)
              this.documentationdata.splice(index,1);
              this.ProductForm2.delProductDocumentationList.push(corRow);
              this.$emit("changeCallBack", this.ProductForm2);
                // console.log("===========productDocumentationList:");
                // console.log(this.ProductForm);
            },
            //
            handleChange2(info) {
                //console.log(info);
                let fileList = [...info.fileList];
                fileList = fileList.slice(-2);
                fileList = fileList.map((file) => {
                    if (file.response) {
                   //console.log(file.response);
                    this.documentationfileId = file.response.data.id
                    this.documentationName = file.response.data.name
                    this.documentationUrl = file.response.data.url
                    }
                    //console.log("===========1:");
                    return file;
                });
                this.documentationFileList = fileList
            },
            //
            handleChange5(info) {
                //console.log(info);
                let fileList = [...info.fileList];
                fileList = fileList.slice(-2);
                fileList = fileList.map((file) => {
                    if (file.response) {
                    //console.log(file.response);
                    this.documentationfileId5 = file.response.data.id
                    this.documentationName5 = file.response.data.name
                    this.documentationUrl5 = file.response.data.url
                    }
                   // console.log("===========2:");
                    //console.log(this.documentationFileList5);

                    return file;
                });
                 
            },
            //
            downloadDocment(corRow){
                 return corRow.fileUrl +'?access_token='+this.$global.access_token;
            },
        },
    }
</script>

<style scoped>
.dialog_form .ant-form-item,.dialog_form2 .ant-form-item {margin-bottom: 14px;}
.dialog_content {padding: 0;}
.ant-form-item-label {
  text-align: right;
  vertical-align: middle;
  line-height: 39.9999px;
  display: inline-block;
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
}
.ant-advanced-search-form {
  padding: 18px;
  background: #fbfbfb;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  margin: 0 20px;
}
.ant-advanced-search-form .ant-form-item-control-wrapper {flex: 1;}
.dialog_form, .dialog_tab{padding: 20px}
.ant-layout-header {
    background: none;
    padding:0px 5px;
    height: 44px;
    line-height: 34px;
    font-size: 16px;
    margin-top: -10px;
}
.title {font-size: 18px;padding: 0px 20px 15px;}
</style>