            onCellChange (corRow, dataIndex, value) {
                const data1 = [... this.ProductForm2.productAttributesList]
                const target = data1.find(item => item.key === corRow.key)

                if (target) {
                    target[dataIndex] = value
                     this.ProductForm2.productAttributesList = data1
