                const newData = {
                    key: this.attributescount,
                    attName: `0`,
                    attValue: `0`,
                }
                 this.ProductForm2.productAttributesList = [...this.ProductForm2.productAttributesList, newData]
                this.attributescount = this.attributescount + 1
            },  
