<template>
    <div>
        <a-button type="primary"   @click="showModal3" style="margin-bottom:15px"></a-button>
        <a-table :columns="drawingcolumns" :dataSource="drawingdata"  :pagination="showPagination"  :rowKey="(v,i)=>{return i}">
            <template slot="fileName" slot-scope="text, record">
                <a :href="downloadDraw(record)"   style="cursor: pointer"><span class="lineEllipsis2">{{text}}</span></a>
            </template>
            <template slot="drawName" slot-scope="text, record">
                <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="drawCode" slot-scope="text, record">
                <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="itemCode" slot-scope="text, record">
                <span class="lineEllipsis2">{{text}}</span>
            </template>
            <template slot="action" slot-scope="text,record,index">
                <a href="javascript:;"  @click="editDrawing(index,record)"> <a-icon type="edit" /></a>
                <a-popconfirm  v-if="drawingdata.length"  title="?"  @confirm="() => onDeleteDrawing(index,record)"  cancelText=""  okText="">
                        <a href="javascript:;"  > <a-icon type="delete" /></a>
                </a-popconfirm>
            </template>
        </a-table>
        <!--  -->
        <a-modal title="" :visible="visible3"  class="dialog_content" width="600px" :confirmLoading="confirmLoading"  @cancel="visible3=false"  @ok="addProductDrawing"  cancelText=""  okText="">
            <div class="dialog_form dialog_form3">
                <a-form :form="addProductDrawingForms2">
                    <a-form-item label="" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }" >
                        <a-input  placeholder="" v-decorator="[ 'drawCode' ]" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 3}" :wrapper-col="{ span: 21 }" >
                        <a-input   placeholder="" v-decorator="[ 'drawName']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }" >
                        <a-input  placeholder="" v-decorator="[ 'itemCode']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }" >
                        <a-upload 
                        name="file"
                        class="avatar-uploader"
                        :action="fileUploadUrl" 
                        :multiple="true" 
                        listType="picture-card"
                        :fileList="drawingFileList" 
                        @change="handleChange1"
                        :beforeUpload="beforeUpload"
                        :showUploadList="false">
                        <img v-if="imageUrl" :src="imageUrl" alt="avatar"  style="width:102px;height:102px"/>
                            <div v-else>
                                <a-icon :type="loading ? 'loading' : 'plus'" />
                                <div class="ant-upload-text"></div>
                            </div>
                        </a-upload>
                    </a-form-item>
                </a-form>
            </div>
        </a-modal>

        <!-- -->
        <a-modal title="" :visible="visible6"  class="dialog_content" width="600px" :confirmLoading="confirmLoading"  @cancel="visible6=false"  @ok="addProductDrawing6"  cancelText=""  okText="">
            <div class="dialog_form dialog_form3">
                <a-form :form="addProductDrawingForms3"> 
                    <a-form-item label="" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }" >
                        <a-input  placeholder="" v-decorator="[ 'drawCode' ]" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 3}" :wrapper-col="{ span: 21 }" >
                        <a-input   placeholder="" v-decorator="[ 'drawName']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }" >
                        <a-input  placeholder="" v-decorator="[ 'itemCode']" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 3 }" :wrapper-col="{ span: 21 }" >
                        <a-upload 
                        name="file"
                        class="avatar-uploader"
                        :action="fileUploadUrl" 
                        :multiple="true" 
                        listType="picture-card"
                        :fileList="drawingFileList6" 
                        @change="handleChange6"
                        :beforeUpload="beforeUpload"
                        :showUploadList="false">
                        <img v-if="imageUrl6" :src="imageUrl6" alt="avatar"  style="width:102px;height:102px"/>
                            <div v-else>
                                <a-icon :type="loading ? 'loading' : 'plus'" />
                                <div class="ant-upload-text"></div>
                            </div>
                        </a-upload>
                    </a-form-item>
                </a-form>
            </div>
        </a-modal>
    </div>
</template>
<script>
   import {getProductInfo} from '@/api/ProductApi';

    function getBase64 (img, callback) {
        const reader = new FileReader()
        reader.addEventListener('load', () => callback(reader.result))
        reader.readAsDataURL(img)
    }

    const drawingcolumns = [
        {
        title: '',
        dataIndex: 'drawName',
        key: 'drawName',
        width:200,
        align:'center',
        scopedSlots: { customRender: 'drawName' },
        }, {
        title: '',
        dataIndex: 'drawCode',
        key: 'drawCode',
        width:200,
        align:'center',
        scopedSlots: { customRender: 'drawCode' },
        }, {
        title: '',
        dataIndex: 'itemCode',
        key: 'itemCode',
        width:200,
        align:'center',
        scopedSlots: { customRender: 'itemCode' },
        }, {
        title: '',
        dataIndex: 'fileName',
        key: 'fileName',
        width:200,
        align:'center',
        scopedSlots: { customRender: 'fileName' },
        }, { 
        title: '',
        width:200,
        align:'center',
        scopedSlots: { customRender: 'action' },
        },
    ];
    export default {
         props: ['productId'],
    components: {},
        data() {
            return {
                showPagination:false,
                drawingFileList: [],
                drawingFileList6: [],
                confirmLoading: false,
                addProductDrawingForms2: this.$form.createForm(this),
                addProductDrawingForms3: this.$form.createForm(this),

                ProductForm2:
                {
                    claCode:"",
                    delProductAttributesList: [
                        // {
                        // "attId:null,
                        // "attIsempty:null,
                        // "attName:"",
                        // "attValue:"",
                        // "orderid:null,
                        // "productCode:""
                        // }
                    ],
                    delProductDocumentationList: [
                        // {
                        // "docCode:"",
                        // "docEdition:"",
                        // "docId:null,
                        // "docLegend:"",
                        // "docName:"",
                        // "docState:null,
                        // "fileId:null,
                        // "productCode:""
                        // }
                    ],
                    delProductDrawingList: [
                        // {
                        // "drawCode:"",
                        // "drawId:null,
                        // "drawName:"",
                        // "fileId:null,
                        // "itemCode:"",
                        // "productCode:""
                        // }
                    ],
                    delProductPackingList: [
                        // {
                        // "addPalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "deletePalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "packHeight:null,
                        // "packId:null,
                        // "packLength:null,
                        // "packStandards:"",
                        // "packVolume:null,
                        // "packWeight:null,
                        // "packWidth:null,
                        // "productCode:"",
                        // "remark:""
                        // }
                    ],
                    productAttributesList: [
                        // {
                        // "attId:null,
                        // "attIsempty:null,
                        // "attName:"",
                        // "attValue:"",
                        // "orderid:null,
                        // "productCode:""
                        // }
                    ],
                    productChart:"",
                    productCode:"",
                    productDescribe:"",
                    productDocumentationList: [
                        // {
                        // "docCode:"",
                        // "docEdition:"",
                        // "docId:null,
                        // "docLegend:"",
                        // "docName:"",
                        // "docState:null,
                        // "fileId:null,
                        // "productCode:""
                        // }
                    ],
                    productDrawingList: [
                        // {
                        // "drawCode:"",
                        // "drawId:null,
                        // "drawName:"",
                        // "fileId:null,
                        // "itemCode:"",
                        // "productCode:""
                        // }
                    ],
                    productId:null,
                    productMaterialQuality:"",
                    productModel:"",
                    productName:"",
                    productPackingList: [
                        // {
                        // "addPalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "deletePalletFormList: [
                        //     {
                        //     "height:"",
                        //     "length:"",
                        //     "load:"",
                        //     "packId:null,
                        //     "palletId:null,
                        //     "type:"",
                        //     "volume:"",
                        //     "width:""
                        //     }
                        // ],
                        // "packHeight:null,
                        // "packId:null,
                        // "packLength:null,
                        // "packStandards:"",
                        // "packVolume:null,
                        // "packWeight:null,
                        // "packWidth:null,
                        // "productCode:"",
                        // "remark:""
                        // }
                    ],
                    productPicture:null,
                    productRoutine: {
                        productBrand:"",
                        productCode:"",
                        productDiameter:"",
                        productImprint:"",
                        productLength:"",
                        productMaterial:"",
                        productMaterialRemark:"",
                        productStandard:"",
                        productSurface:"",
                        productSurfaceRemark:"",
                        productToothPitch:"",
                        productToothType:"",
                        productUnit:"",
                        productWeight:"",
                        remark:"",
                        routId:null
                    },
                    productSpecs:""
                },
                drawingdata:[],
                drawingcolumns,
                visible6: false,
                visible3: false,
                drawingfileId:null,
                drawingfileId6:null,
                productfileId:null,
                drawingfileUrl: '',
                drawingfileUrl6: '',
                loading: false,
                imageUrl: '',
                imageUrl6: '',
                curproductCode:null,
                
            }
        },
        watch: {}, 
        computed: {
            fileUploadUrl() {
                return this.$global.baseUrl + '/file/upload' + '?access_token=' + this.$global.access_token;
            },
        },
        created(){
           this.getProductInfos(this.productId);
           
        },
        methods: {
            //
             editDrawing(i,v){
                this.visible6=true;
                this.editDrawTemp=i
                this.drawingfileId = v.fileId
                this.imageUrl6 = v.pictureUrl
                let {
                        drawCode,
                        drawName,
                        itemCode
                    }=v
                    setTimeout(() => {
                    this.addProductDrawingForms3.setFieldsValue({
                        drawCode,
                        drawName,
                        itemCode
                        })
                }, 0);
            },
            addProductDrawing6(e){
                e.preventDefault();
                this.addProductDrawingForms3.validateFields((err, values) => {
                if (!err) {
                    //console.log('Received values of form: ', values);

                    const form = values;
                     if(this.drawingfileId6){
                        form.fileId = this.drawingfileId6;
                        form.fileName = this.drawingfileName6;
                        form.fileUrl = this.drawingfileUrl6
                        form.pictureUrl = this.imageUrl6
                    }
                    if(this.productId !== -1){
                         form.drawId=this.drawingdata[this.editDrawTemp].drawId
                        }
                   
                    //console.log("this.addProductDocumentationForms productCode")
                    //console.log(form.productCode)
                    form.productCode= this.curproductCode
                    this.drawingdata[this.editDrawTemp]=values;
                    this.visible6 = false

                    this.ProductForm2.productDrawingList[this.editDrawTemp]=form;
                    this.$emit("changeCallBack", this.ProductForm2);
                    this.drawingfileId6=null
                    this.drawingfileId=null
                    this.drawingfileUrl6=null
                    this.imageUrl6 =null

                    // console.log("===========addProductDrawingForms:");
                    // console.log(this.ProductForm2);
                }
                });
            },

          
            //productId
            getProductInfos(corId){
                if(corId !== -1){
                    getProductInfo(corId).then((res) => {
                        if (res.data.code === this.$global.successCode) {
                        //console.log("",res);

                        //
                        this.drawingdata = res.data.data.productDrawingList
                        this.curproductCode = res.data.data.productCode

                        }else{
                            this.$message.error(res.data.message);
                        }
                        }, function (error) {
                            console.log("", error);
                    });
                }
                
            },

            showModal3() {
                this.addProductDrawingForms2.resetFields();
                this.drawingFileList = []
                this.visible3 = true
            },

            //
            downloadDraw(corRow){
               return corRow.fileUrl +'?access_token='+this.$global.access_token;
            },

            //
            addProductDrawing(e){
                e.preventDefault();
                this.addProductDrawingForms2.validateFields((err, values) => {
                if (!err) {
                    //console.log('Received values of form: ', values);
                    const form = values;
                    if(this.drawingfileId){
                        form.fileId = this.drawingfileId;
                        form.fileName = this.drawingfileName;
                        form.fileUrl = this.drawingfileUrl
                        form.pictureUrl = this.imageUrl
                    }
                    //console.log(form);
                    this.drawingdata.push(form);
                    this.visible3 = false

                    form.productCode= this.curproductCode

                   // console.log("this.productDrawingList productCode")
                   // console.log(form.productCode)
                    this.ProductForm2.productDrawingList.push(form);
                    this.$emit("changeCallBack", this.ProductForm2);
                    // console.log("===========productDrawingList:");
                    // console.log(this.ProductForm2);
                    this.drawingfileId = null;
                    this.drawingfileName = null;
                    this.drawingfileUrl = null
                    this.imageUrl = null
                }
                });
            },
            
            //table
            onDeleteDrawing(index,corRow){
                this.ProductForm2.delProductDrawingList.push(corRow);
                this.drawingdata.splice(index,1);
                this.$emit("changeCallBack", this.ProductForm2);
                // console.log("===========productDrawingList:");
                // console.log(this.ProductForm2);
            },
            //
            handleChange1(info) {
                //console.log(info);
                let fileList = [...info.fileList];
                fileList = fileList.slice(-2);
                fileList = fileList.map((file) => {
                    if (file.response) {
                       
                    //console.log(file.response)
                    getBase64(info.file.originFileObj, (imageUrl) => {
                        this.imageUrl = imageUrl
                        //console.log(this.imageUrl)
                        })

                        this.drawingfileId = file.response.data.id
                        this.drawingfileName = file.response.data.name
                        this.drawingfileUrl = file.response.data.url

                    }
                   // console.log("===========1:");
                    return file;
                });
                this.drawingFileList = fileList
                //console.log(this.drawingFileList)
            },
            //
            handleChange6(info) {
                //console.log(info);
                let fileList = [...info.fileList];
                fileList = fileList.slice(-2);
                fileList = fileList.map((file) => {
                    if (file.response) {
                     
                    getBase64(info.file.originFileObj, (imageUrl) => {
                        this.imageUrl6 = imageUrl
                        })

                        this.drawingfileId6 = file.response.data.id
                        this.drawingfileName6 = file.response.data.name
                        this.drawingfileUrl6 = file.response.data.url
                    }
                    return file;
                });
                this.drawingFileList6 = fileList
            },

            callback (key) {},

            beforeUpload (file) {
                    const isJPG = file.type === 'image/jpeg'
                    const isPNG = file.type === 'image/png'
                    if (!isJPG && !isPNG) {
                        this.$message.error('jpgpng!')
                    }
                    const isLt2M = file.size / 1024 / 1024 < 2
                    if (!isLt2M) {
                        this.$message.error('2MB!')
                    }
                    return isJPG || isPNG && isLt2M
                },
        },
    }
</script>

<style scoped>
.dialog_form .ant-form-item,.dialog_form2 .ant-form-item { margin-bottom: 14px;}
.ant-breadcrumb {padding: 0;position: absolute;top: 10px;right: 20px;}
.dialog_content {padding: 0;}
.ant-form-item-label {
  text-align: right;
  vertical-align: middle;
  line-height: 39.9999px;
  display: inline-block;
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
}
.ant-advanced-search-form {
  padding: 18px;
  background: #fbfbfb;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  margin: 0 20px;
}
.ant-advanced-search-form .ant-form-item-control-wrapper {flex: 1;}
.dialog_form, .dialog_tab{padding: 20px}
.ant-layout-header {
    background: none;
    padding:0px 5px;
    height: 44px;
    line-height: 34px;
    font-size: 16px;
    margin-top: -10px;
}
.title {font-size: 18px; padding: 0px 20px 15px;}
</style>