<template>
    <div>
        <a-layout>
            <a-layout-content  style="flex: 0 0 900px;min-width:900px;max-width: 900px;width: 900px;">
                <a-form :form="productRoutineForm2">
                    <a-form-item label=""  :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productUnit" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productStandard" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productLength" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productDiameter" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productImprint" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productBrand" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productMaterial" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productMaterialRemark" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productToothType" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productToothPitch" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productSurface" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productSurfaceRemark" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.productWeight" />
                    </a-form-item>
                    <a-form-item label="" :label-col="{ span: 5 }" :wrapper-col="{ span: 19 }" >
                        <a-input  placeholder="" v-model="productRoutineForm2.remark" />
                    </a-form-item>
                </a-form>
            </a-layout-content>
            <a-layout-sider style="background:none;height:180px;flex: 0 0 200px;"></a-layout-sider>
        </a-layout>
    </div>
</template>
<script>
    import {getProductInfo} from '@/api/ProductApi';
    export default {
        props: ['productId'],
        data() {
            return {
                showPagination: false,
                confirmLoading: false,
                loading: false,
                curproductCode: null, 
                productRoutineForm2:{
                    productBrand: "",        //
                    productDiameter: "",        //
                    productCode: null,        //id
                    productImprint: "",        //
                    productLength: "",        //
                    productMaterial: "",        //
                    productMaterialRemark: "",        //
                    productStandard: "",        //
                    productSurface: "",        //
                    productSurfaceRemark: "",        //
                    productToothPitch: "",        //
                    productToothType: "",        //
                    productUnit:"",        //
                    productWeight: "",        //
                    remark: "",        //
                    routId:null        //id
                }
            }
        },
        created(){
            this.getProductInfos(this.productId)
        },
        methods: {
            //productId
            getProductInfos(corId){
                if(this.productId !== -1){
                     getProductInfo(corId).then((res) => {
                        if (res.data.code === this.$global.successCode) {
                        console.log("",res);
                        this.productRoutineForm2 = res.data.data.productRoutine
                        this.curproductCode = res.data.data.productCode
                        console.log(this.productRoutineForm2)
                        }else{
                            this.$message.error(res.data.message);
                        }
                        }, function (error) {
                            console.log("", error);
                    });
                }
                this.productRoutineForm2.productCode = this.curproductCode
            },
        },
    }
</script>

<style scoped>
.ant-form-item-label {text-align: right; vertical-align: middle;line-height: 39.9999px;display: inline-block;
overflow: hidden;overflow-x: hidden; overflow-y: hidden; white-space: nowrap;}
.ant-layout-header {background: none;padding:0px 5px;height: 44px;line-height: 34px;font-size: 16px;margin-top: -10px;}
.title {font-size: 18px;padding: 0px 20px 15px;}
</style>
      

